<template>
	<div class="container">
		<v-back :title="$route.query.type == 'set' ? '黄名单信息修改' : '添加黄名单'" />
		<div class="scroll-view">
			<md-field>
				<div class="md-field-item is-solid">
					<div class="md-field-item-content" style="padding: 0;">
						<label class="md-field-item-title">* 图片</label>
						<div class="md-field-item-control">
							<ul class="image-reader-list">
								<li
								class="image-reader-item"
								v-for="(img, index) in imageList['tupian']"
								:key="index"
								:style="{
									'backgroundImage': `url(${img})`,
									'backgroundPosition': 'center center',
									'backgroundRepeat': 'no-repeat',
									'backgroundSize': 'cover'
								}"
								@click="imgsShow(img)">
								<md-tag
									class="image-reader-item-del"
									size="small"
									shape="quarter"
									fill-color="#111A34"
									type="fill"
									font-color="#fff"
									@click.native.stop="onDeleteImage('tupian', index)"
								>
									<md-icon name="close"></md-icon>
								</md-tag>
								</li>
								<li class="image-reader-item add" v-show="imageList.tupian.length == 0">
									<md-image-reader
										name="tupian"
										@select="onReaderSelect"
										@complete="onReaderComplete"
										@error="onReaderError"
									>
									</md-image-reader>
									<md-icon name="camera" size="md" color="#CCC"></md-icon>
									<p>添加图片</p>
								</li>
							</ul>
						</div>
					</div>
				</div>
				
				<md-input-item
				  ref="input0"
				  title="* 姓名"
				  placeholder="请输入姓名"
				  is-amount
				  :maxlength="15"
				  v-model="visitor_name">
				</md-input-item>
				
				<md-input-item
				  ref="input2"
				  title="联系方式"
				  placeholder="0-40个字符"
				  is-amount
				  :maxlength="40"
				  v-model="phone">
				</md-input-item>
				
				<md-input-item
				  ref="input6"
				  title="备注"
				  placeholder="0-40个字符"
				  is-amount
				  :maxlength="40"
				  v-model="remark">
				</md-input-item>
				
				
			</md-field>
			<md-field>
				<md-button type="primary" size="small" round @click="submit">提交保存</md-button>
			</md-field>
		</div>
		<!-- 图片预览 -->
		<md-image-viewer
			v-model="isViewerShow"
			:list="imgs"
			:has-dots="true">
		</md-image-viewer>
	</div>
</template>

<script>
	import axios from 'axios'
	import compressImage from 'ls-compressimage'
	export default {
		name: 'add_yellow',
		data() {
			return {
				isViewerShow: false,
				imgs: [],
				id: '',
				yellow_list_nbr: '',
				visitor_name: '',//姓名
				phone: '',//联系方式
				remark: '',//备注
				imageList: {
				    tupian: []
				},
				blob: null,
				file: {}
			}
		},
		methods: {
			imgsShow(img) {
				this.isViewerShow = true
				this.imgs = [img]
			},
			onReaderSelect(name, {files}) {
			    files.forEach(file => {
			        console.log('[Mand Mobile] ImageReader Selected:', 'File Name ' + file.name)
			    })
			    this.$toast.loading('图片读取中...')
			},
			onReaderComplete(name, {dataUrl, blob, file}) {
			    this.$toast.hide()
			    console.log('[Mand Mobile] ImageReader Complete:', 'File Name ' + file.name)
			    setTimeout(() => {
			        const demoImageList = this.imageList[name] || []
			        demoImageList.push(dataUrl)
			        this.$set(this.imageList, name, demoImageList)
			        this.blob = blob
			        this.file = file
			    }, 100)
			},
			onReaderError(name, {msg}) {
			    this.$toast.failed(msg)
			},
			onDeleteImage(name, index) {
			    const demoImageList = this.imageList[name] || []
			    demoImageList.splice(index, 1)
			    this.$set(this.imageList, name, demoImageList)
			},
			submit() {
				let params = {
					visitor_name: this.visitor_name,
					visitor_type: 'Y',
					phone: this.phone,
					remark: this.remark,
					visitor_photo: this.visitor_photo
				}
				
				//return;
				if (this.visitor_name == '') {
					this.$toast.failed('请输入姓名')
				} else if (this.imageList.tupian.length == 0) {
					this.$toast.failed('请上传图片')
				} else {
					if (this.blob == null) {
						this.updateStudent(params)
					} else {
						this.$toast.loading('图片上传中...');
						new compressImage({
							res: this.file, // 必选 资源
							justConversion: false, // 可选 声明了此字段说明只进行格式转换 res requestType responseType callback必填，其他随意
							width: 300, // 可选(justConversion存在可不选) (默认图片自身宽度,建议自定义) 压缩后图片宽度
							//height: 300,//可选 (默认 自定义图片宽度/原生图片宽度*原生图片高度) 压缩后图片高度
							quality: 0.1, // 可选 (默认0.3) 压缩层级质量（0<quality<1）越大压缩越快 图片质量跨度相对低 越小则相反
							size: 500, // 必选(justConversion存在可不选) 压缩后图片大小 单位k
							requestType: 'file', // 必选 传入文件类别 base64 | file | blob
							responseType: 'file', // 必选 返回文件类别 base64 | file | blob
							fileName: 'yellow', // 如果responseType的类型为file和非justConversion 此选项可选 返回文件对象的文件名
							callback: (result) => {
							  if (result === null) {
								this.$toast.failed("图片上传失败")
								return
							  }
							  console.log(result.size / 1024)
							  var formData = new FormData();
							  formData.append("file", result, new Date().getTime() + '.' + result.type.split('/')[1]);
							  axios.post('http://106.12.3.212/dorm/yellow/UploadImg/-1', formData, { headers: {'Content-Type': 'multipart/form-data'} }).then( res => {
							  	  if (res.data.code == 0) {
							  		  params.photo = res.data.url
									  this.visitor_photo = res.data.url
									  params.visitor_photo = res.data.url
							  		  setTimeout(() => {
							  			this.updateStudent(params)
							  		  }, 500)
							  	  } else {
							  		  this.$toast.failed("图片上传失败")
							  	  }
							  }).catch((err) => {
							  	this.$toast.failed("图片上传失败")
							  })
							  /* axios.post(this.$baseUrl() + '/users/upload/yellow', formData, { headers: {'Content-Type': 'multipart/form-data'} }).then( res => {
							  	console.log(res.data.data)
							  	this.visitor_photo = res.data.data
								params.visitor_photo = res.data.data
								setTimeout(() => {
									this.updateStudent(params)
								}, 500)
							  }).catch((err) => {
							  	this.$toast.failed("图片上传失败")
							  }) */
							} // 回调函数 返回处理后文件 如果为null 表示文件处理不了(原因 少了参数 参数不合法))
						})
					}
					
				}
			},
			updateStudent(params) {
				if (this.$route.query.type == 'set') {
					params.id = this.id
					params.usernbr = this.yellow_list_nbr
				} else {
					params.usernbr = (new Date()).valueOf()
				}
				this.$toast.loading('数据提交中...');
				this.$post('/users/yellow_add', params, ()=>{
				    this.$toast.succeed("提交成功")
					setTimeout(() => {
						this.$router.back()
					}, 1000)
				})
			}
		},
		mounted() {
			if (this.$route.query.type == 'set') {
				document.title = window.configs.headertitle + '黄名单信息修改'
				this.$get('/users/person_yellow', {
					usernbr: this.$route.query.usernbr
				}, (data)=>{
				    console.log(data.data[0])
					let user = data.data[0]
					this.id = user.id
					this.visitor_name = user.yellow_list_name
					this.phone = user.phone
					this.remark = user.remark
					this.yellow_list_nbr = user.yellow_list_nbr
					this.imageList.tupian = user.yellow_list_photo ? [user.yellow_list_photo] : [],
					this.visitor_photo = user.yellow_list_photo ? user.yellow_list_photo : ''
				})
			}
		}
	}
</script>

<style scoped="scoped">
	.image-reader-list {
	     width: 100%;
	     margin-top: 3vw;
		 clear: both;
	 }
	 .image-reader-list .image-reader-item {
	     position: relative;
	     width: 25vw;
	     height: 25vw;
	     padding-bottom: 23.5%;
	     margin-bottom: 2%;
	     margin-right: 2%;
	     background: #FFF;
	     box-shadow: 0 5px 20px rgba(197, 202, 213, .25);
	     box-sizing: border-box;
	     list-style: none;
	     border-radius: 4px;
	     background-size: cover;
	     overflow: hidden;
	 }
	 .image-reader-list .image-reader-item .md-tag {
	     position: absolute;
	     right: 0;
	 }
	 .image-reader-item.add .md-icon {
	      position: absolute;
	      top: 40%!important;
	      left: 50%;
	      transform: translate(-50%, -50%);
	      opacity: .5;
	  }
	 .image-reader-item.add p {
	      position: absolute;
	      top: 35%;
	      left: 0;
	      width: 100%;
	      margin-top: 15px;
	      font-size: 22px;
	      color: #CCC;
	      text-align: center;
	      font-size: 12px;
	  }
	 .md-drop-menu {
		 position: relative;
	 }
	 .md-drop-menu >>> .md-popup {
		 z-index: 1201;
	 }
	 .md-drop-menu >>> .md-drop-menu-bar {
		 background: none;
	 }
	 .md-drop-menu >>> .md-drop-menu-bar:before {
		 display: none;
	 }
	 .md-drop-menu >>> .md-drop-menu-bar .bar-item {
		 justify-content: left;
	 }
	 .scroll-view {
		height: calc(100vh - 10vw);
		overflow-y: auto;
	 }
</style>
